// FOCUS EFFECTS

.fc-o { outline: 12px solid transparent; outline-offset: 2px; }
.fc-o:focus { outline: 2px solid var(--color-blue-2); }
.js-focus-visible .fc-o:not(.focus-visible):focus { outline-color: transparent; }

.fc-od { outline: 12px solid transparent; outline-offset: 2px; }
.fc-od:focus { outline: 2px solid var(--color-gray-7); }
.js-focus-visible .fc-od:not(.focus-visible):focus { outline-color: transparent; }

.fc-s { box-shadow: 0 0 0 12px transparent; }
.fc-s:focus { box-shadow: 0 0 0 2px var(--color-blue-1); }
.fc-b:focus { border-color: var(--color-blue-4); }

.fc-on:focus { outline: none; }

