// FONT FAMILY GROUPS
.ff-sans {
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", /* Mac */
               "Ubuntu", "Roboto", /* Linux */
               "Segoe UI", /* Window */
               sans-serif;
}

.ff-mono {
  font-family: "SFMono-Regular", Menlo, /* Mac */
               "Ubuntu Mono", "Liberation Mono", /* Linux */
               Consolas, Courier, /* Window */
               monospace;
}
