// Defining one by one like this is not good, but currently
// we haven't known a better way yet since multiple properties
// must go in one "transition" property

.trns-a {
    transition: all .1s;
}

.trns-o {
    transition: all .1s, outline .3s ease;
}

.trns-s {
    transition: all .1s, box-shadow .3s ease;
}
